<template>
  <section>
    <div class="navbar-container box-shadow">
      <div class="container-fluid">
        <div class="navbar-header">
           <span class="page-title">{{title}}</span>
        </div>
      </div>
    </div>
    <div class="content">
      <el-row class="tbl-group tbl-group-card">
          <el-col>
              <el-card class="background-fff card-table">
                  <el-row class="table-layout">
                      <el-col><label>关联资源</label></el-col>
                      <el-col>
                          <el-row class="table-layout">
                              <el-col><span>产品类型：</span></el-col>
                              <el-col>
                                  <el-select placeholder="请选择产品类型" v-model="productDefault" value-key="type" :disabled="action_type == 'update'">
                                    <el-option v-for="m in product_list" :key="m.type" :label="m.name" :value="m">{{m.name}}</el-option>
                                  </el-select>
                              </el-col>
                          </el-row>
                          <el-row class="table-layout">
                              <el-col><span>资源范围：</span></el-col>
                              <el-col>
                                  <el-select v-model="resource_type" :disabled="action_type == 'update'">
                                    <el-option :value="0" label="全部资源"></el-option>
                                    <el-option :value="1" label="实例"></el-option>
                                  </el-select>
                              </el-col>
                          </el-row>
                          <el-row class="table-layout" v-if="resource_type == 1 && action_type == 'add'">
                              <el-col><span>虚拟数据中心：</span></el-col>
                              <el-col>
                                  <el-select v-model="currentApp" value-key="id">
                                    <el-option v-for="m in app_list" :key="m.id" :label="m.name" :value="m"></el-option>
                                  </el-select>
                              </el-col>
                          </el-row>
                          <el-row class="table-layout" v-if="resource_type == 1">
                              <el-col><span>实例信息：</span></el-col>
                              <el-col>
                                  <div v-if="action_type == 'add'">
                                    <el-select v-model="instanceTypeDefault" value-key="type">
                                        <el-option v-for="m in instance_type_list" :key="m.type" :label="m.name" :value="m">{{m.name}}</el-option>
                                    </el-select>
                                    <el-input :placeholder="'请输入' + instanceTypeDefault.name" style="width: 250px;margin-left:10px;" v-model="keyword">
                                        <el-button slot="append" @click="search"><i class="el-icon-search"></i></el-button>
                                    </el-input>
                                  </div>
                                  <div class="border m-top20 box" v-if="instance_list.length > 0">
                                    <div v-if="action_type == 'add'">
                                      <el-checkbox-group class="group-checkbox" v-model="instances" v-if="productDefault.type == 'cloud_vm'" @change="FnSetInstance">
                                          <el-checkbox v-for="(m, i) in instance_list" :key="i" :label="m.vm_id" :disabled="action_type == 'update'">{{m.name}}</el-checkbox>
                                      </el-checkbox-group>
                                      <el-checkbox-group class="group-checkbox" v-model="instances" v-else @change="FnSetInstance">
                                          <el-checkbox v-for="(m, i) in instance_list" :key="i" :label="m.id" :disabled="action_type == 'update'">{{m.name}}</el-checkbox>
                                      </el-checkbox-group>
                                    </div>
                                    <div v-else>
                                      <el-checkbox-group class="group-checkbox" v-model="instances">
                                          <el-checkbox v-for="(m, i) in instance_list" :key="i" :label="m.instance_id" :disabled="true">{{m.name}}</el-checkbox>
                                      </el-checkbox-group>
                                    </div>  
                                  </div>
                                  <div class="border m-top20 box" style="text-align:center;" v-if="action_type == 'add' && instance_list.length <= 0">
                                    <div class="m-top10">暂无数据</div>
                                  </div>
                                  <p class="m-top10" v-if="resource_type == 1 && action_type == 'add'">当前选了{{total_instances}}个实例</p>
                              </el-col>
                          </el-row>
                      </el-col>
                  </el-row>
              </el-card>
               <el-card class="background-fff card-table">
                  <el-row class="table-layout">
                      <el-col><label>报警规则设置</label></el-col>
                      <el-col>
                          <div v-for="(item, j) in rules" :key="j">
                            <hr class="normal" v-if="j > 0" />  
                            <el-row class="table-layout">
                                <el-col><span>规则名称：</span></el-col>
                                <el-col>
                                    <el-input style="width:15rem;" v-model="item.rule_name" @change="filterName(item.rule_name, j)"></el-input>
                                    <span v-if="j > 0" class="light pointer pull-right" @click="FnDeleteRule(j)">删除</span>
                                </el-col>
                            </el-row>
                            <el-row class="table-layout">
                                <el-col><span>规则描述：</span></el-col>
                                <el-col>
                                    <el-select v-model="item.metrics_id" value-key="value" class="rule-select">
                                        <el-option v-for="m in metrice_list" :key="m.value" :label="m.name" :value="m">{{m.name}}</el-option>
                                    </el-select>
                                    <el-select v-model="item.period" value-key="value" class="rule-select">
                                        <el-option v-for="m in period_list" :key="m.value" :label="m.name" :value="m.value">{{m.name}}</el-option>
                                    </el-select>
                                    <el-select v-model="item.lasted_period" value-key="value" class="rule-select">
                                        <el-option v-for="m in lastedPeriod_list" :key="m.value" :label="m.name" :value="m.value">{{m.name}}</el-option>
                                    </el-select>
                                    <el-select v-model="item.trigger_type" value-key="value" class="rule-select m-top10">
                                        <el-option v-for="m in trigger_type_list" :key="m.value" :label="m.name" :value="m.value">{{m.name}}</el-option>
                                    </el-select>
                                    <el-select v-model="item.trigger_symbol"  class="rule-select m-top10">
                                        <el-option v-for="(m, i) in trigger_symbol_list" :key="i" :label="m" :value="m"></el-option>
                                    </el-select>
                                    <el-input-number v-model="item.value" class="rule-select" placeholder="阈值" controls-position="right" :min="0"></el-input-number>
                                    <span class="rule-select" v-if="item.metrics_id">{{item.metrics_id.unit}}</span>
                                </el-col>
                            </el-row>       
                          </div>
                          <el-row class="table-layout" v-if="action_type == 'add'">
                              <el-col></el-col>
                              <el-col>
                                 <span class="light pointer" @click="FnaddRules"><i class="el-icon-plus"></i>添加报警规则</span>
                              </el-col>
                          </el-row>  
                          <el-row class="table-layout">
                              <el-col><span>通道沉默周期
                                  <el-tooltip content="通道沉默周期指报警发生后如果未恢复正常，间隔多久重复发送一次报警通知。最短为5分钟，最长为24小时。" placement="bottom" effect="light">
                                       <i class="el-icon-warning-outline light"></i>
                                  </el-tooltip>
                                ：</span></el-col>
                              <el-col>
                                 <el-select v-model="silent_period" value-key="value">
                                    <el-option v-for="m in silent_period_list" :key="m.vlaue" :label="m.name" :value="m.value"></el-option>
                                  </el-select>
                              </el-col>
                          </el-row>
                          <el-row class="table-layout">
                              <el-col>
                                <span>生效时间
                                  <el-tooltip content="报警规则的生效时间指报警规则只在生效时间内发送报警通知，非生效时间内产生的报警只记录报警历史。" placement="bottom" effect="light">
                                       <i class="el-icon-warning-outline light"></i>
                                    </el-tooltip>
                                ：</span>
                                </el-col>
                              <el-col>
                                  <el-time-select
                                    style="width:150px;"
                                    v-model="active_start_time"
                                    :picker-options="{
                                        start: '00:00',
                                        step: '01:00',
                                        end: '23:00'
                                    }" placeholder="选择开始时间"></el-time-select>
                                    <span class="m-left10">至</span>
                                    <el-time-select
                                    class="m-left10"
                                    style="width:150px;"
                                    v-model="active_end_time"
                                    :picker-options="{
                                        start: '00:59',
                                        step: '01:00',
                                        end: '23:59'
                                    }" placeholder="选择开始时间"></el-time-select>
                              </el-col>
                          </el-row>
                      </el-col>
                  </el-row>
              </el-card>
               <el-card class="background-fff card-table">
                  <el-row class="table-layout">
                      <el-col><label>通知方式</label></el-col>
                      <el-col>
                          <el-row class="table-layout">
                              <el-col><span>通知对象：</span></el-col>
                              <el-col>
                                    <el-transfer
                                            style="text-align: left; display: inline-block"
                                            v-model="selected_group"
                                            filterable
                                            :titles="['已有联系人组', '已选联系人组']"
                                            :button-texts="['', '']"
                                            :format="{
                                                noChecked: '${total}',
                                                hasChecked: '${checked}/${total}'
                                            }"
                                            :data="noSelected_group">
                                        <el-button class="transfer-footer btn--full" type="primary" slot="left-footer" size="small" @click="add_group()">快速创建联系人组</el-button>
                                    </el-transfer>
                              </el-col>
                          </el-row>
                          <el-row class="table-layout">
                              <el-col><span>报警级别：</span></el-col>
                              <el-col>
                                  <el-radio-group v-model="level">
                                      <el-radio :label="1" :value="1">
                                          <span>Critical（严重）</span>
                                      </el-radio>
                                      <el-radio :label="2" :value="2">
                                          <span>Warning（警告）</span>
                                      </el-radio>
                                      <el-radio :label="3" :value="3">
                                          <span>Info（普通）</span>
                                      </el-radio>
                                  </el-radio-group>
                              </el-col>
                          </el-row>
                      </el-col>
                  </el-row>
              </el-card>
          </el-col>
          <el-col class="col-cart">
              <el-card class="card--cost card-table">
                  <el-button class="btn--full cds-button" type="primary" @click="createRules">确定</el-button>
              </el-card>
          </el-col>
      </el-row>
    </div>
  </section>
</template>
<script>
import ElCard from "@/components/card";
import { getAppList, getBmsList } from "@/service/monitor/bms_monitor";
import {
  getVmList,
  createRules,
  updateRule,
  getRulesList,
  getGroups,
  linkInstance
} from "@/service/alarm/alarm_rules";
import { Message, MessageBox } from "element-ui";
export default {
  components: { ElCard },
  data() {
    return {
      active_start_time: "",
      active_end_time: "",
      level: 1,
      notifyList: [],
      app_list: [],
      currentApp: null,
      keyword: "",
      isShow: true,
      delLabel: [],
      rules: [
        {
          rule_name: "",
          metrics_id: null,
          period: 1,
          lasted_period: 1,
          trigger_type: "",
          trigger_symbol: "",
          value: ""
        }
      ],
      title_list: ["联系人通知组", "已选组"],
      instance_type_list: [
        { name: "实例名称", type: "name" },
        { name: "实例ID", type: "id" },
        { name: "公网IP", type: "public_ip" },
        { name: "私网IP", type: "private_ip" }
      ],
      instanceTypeDefault: null,
      instance_list: [],
      instances: [],
      product_list: [
        { name: "云服务器（CDS）", type: "cloud_vm" },
        { name: "裸金属（BMS）", type: "bms" }
      ],
      productDefault: null,
      metrice_list: [
        { name: "CPU使用率", value: "cpu.usage", unit: "%" },
        { name: "内存使用率", value: "mem.usage", unit: "%" },
        { name: "磁盘使用率", value: "disk.usage", unit: "%" },
        { name: "磁盘IOPS（读）", value: "disk.iops.read", unit: "IOPS" },
        { name: "磁盘IOPS（写）", value: "disk.iops.write", unit: "IOPS" },
        { name: "磁盘吞吐量（读）", value: "disk.throughput.read", unit: "Kbps" },
        { name: "磁盘吞吐量（写）", value: "disk.throughput.write", unit: "Kbps" },
        { name: "出网流量", value: "nic.throughput.read", unit: "Mbps" },
        { name: "入网流量", value: "nic.throughput.write", unit: "Mbps" }
      ],
      period_list: [
        { name: "1分钟周期", value: 1 },
        { name: "15分钟周期", value: 15 },
        { name: "30分钟周期", value: 30 },
        { name: "60分钟周期", value: 60 }
      ],
      lastedPeriod_list: [
        { name: "连续1周期", value: 1 },
        { name: "连续3周期", value: 3 },
        { name: "连续5周期", value: 5 },
        { name: "连续10周期", value: 10 },
        { name: "连续15周期", value: 15 },
        { name: "连续30周期", value: 30 }
      ],
      trigger_type_list: [
        { name: "平均值", value: "average" },
        { name: "总是", value: "always" },
        { name: "只要有一次", value: "once" }
      ],
      trigger_symbol_list: [">=", ">", "<=", "<"],
      silent_period_list: [
        { name: "5分钟", value: 5 },
        { name: "10分钟", value: 10 },
        { name: "15分钟", value: 15 },
        { name: "30分钟", value: 30 },
        { name: "60分钟", value: 60 },
        { name: "3小时", value: 180 },
        { name: "6小时", value: 360 },
        { name: "12小时", value: 720 },
        { name: "24小时", value: 1440 }
      ],
      silent_period: 5,
      action_type: "",
      rule_id: "",
      noSelected_group: [],
      selected_group: [],
      groupList: [],
      resource_type: 0,
      title: "",
      total_instances: 0
    };
  },
  created() {
    this.action_type = this.$route.params.type;
    if (this.action_type === "update") {
      this.instanceTypeDefault = this.instance_type_list[0];
      this.rule_id = this.$route.params.id;
      this.title = "修改报警规则";
      this.FnGetRuleDetail();
    } else {
      this.title = "创建报警规则";
      this.init();
    }
  },
  methods: {
    init() {
      this.productDefault = this.product_list[1];
      this.instanceTypeDefault = this.instance_type_list[0];
      this.rules[0].metrics_id = this.metrice_list[0];
      this.rules[0].period = this.period_list[0].value;
      this.rules[0].lasted_period = this.lastedPeriod_list[0].value;
      this.rules[0].trigger_type = this.trigger_type_list[0].value;
      this.rules[0].trigger_symbol = this.trigger_symbol_list[0];
      let length = Number(this.silent_period_list.length) - 1;
      this.silent_period = this.silent_period_list[length].value;
      this.active_start_time = "00:00";
      this.active_end_time = "23:59";
      this.FnGetGroupList();
    },
    async FnGetAppList() {
      this.currentApp = null;
      this.app_list = [];
      let res = await getAppList();
      if (res.code === 200) {
        this.app_list = res.data;
        this.app_list.forEach(item => {
          this.$set(item, "instances_list", []);
        });
        this.currentApp = this.app_list[0];
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    async FnGetBmsList(data) {
      this.bms_list = [];
      this.currentBms = null;
      let reqData = {
        appId: this.currentApp.id,
        ipString: this.ip_string,
        current: 1,
        size: 10000
      };
      reqData = Object.assign({}, reqData, data);
      let res = await getBmsList(reqData);
      if (res.code === "Success") {
        let data = res.data ? res.data.records : [];
        data.forEach(item => {
          this.$set(item, "check", false);
        });
        this.instance_list = data;
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    async FnGetVmList(data) {
      // keyword (名称/id) pub_ip ,pri_ip
      let reqData = Object.assign(
        {},
        { page_index: 1 },
        { page_size: 100000 },
        { page_flag: false },
        { app_id: this.currentApp.id },
        data
      );
      let res = await getVmList(reqData);
      if (res.status === 1) {
        this.instance_list = res.data ? res.data.vm_list : [];
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    search() {
      let obj = {};
      this.instances = [];
      this.instance_list = [];
      switch (this.instanceTypeDefault.type) {
        case "name":
          this.productDefault.type === "bms"
            ? (obj["name"] = this.keyword)
            : (obj["keyword"] = this.keyword);
          break;
        case "id":
          this.productDefault.type === "bms"
            ? (obj["id"] = this.keyword)
            : (obj["keyword"] = this.keyword);
          break;
        case "public_ip":
          this.productDefault.type === "bms"
            ? (obj["ipString"] = this.keyword)
            : (obj["pub_ip"] = this.keyword);
          break;
        case "private_ip":
          this.productDefault.type === "bms"
            ? (obj["ipString"] = this.keyword)
            : (obj["pri_id"] = this.keyword);
          break;
        default:
          break;
      }
      if (this.productDefault.type === "bms") {
        this.FnGetBmsList(obj);
      } else {
        this.FnGetVmList(obj);
      }
    },
    FnaddRules() {
      this.rules.push({
        rule_name: "",
        metrics_id: this.metrice_list[0],
        period: this.period_list[0].value,
        lasted_period: this.lastedPeriod_list[0].value,
        trigger_type: this.trigger_type_list[0].value,
        trigger_symbol: this.trigger_symbol_list[0],
        value: ""
      });
    },
    FnDeleteRule(index) {
      this.rules.splice(index, 1);
    },
    async FnGetRuleDetail() {
      let reqData = {
        rule_id: this.rule_id
      };
      let res = await getRulesList(reqData);
      if (res.code === "Success") {
        let data = res.data[0];
        this.product_list.forEach(item => {
          if (item.type === data.product_type) {
            this.productDefault = item;
          }
        });
        if (data.scope === 1) {
          this.FnGetLinkInstance();
        }
        let list = data.metrics_id.split(".");
        let metrics_id = list[1] + "." + list[2];
        this.metrice_list.forEach(item => {
          if (item.value === metrics_id) {
            this.rules[0].metrics_id = item;
          }
        });
        this.rules[0].rule_name = data.name;
        this.rules[0].period = data.period;
        this.rules[0].lasted_period = data.lasted_period;
        this.rules[0].trigger_type = data.trigger_type;
        this.rules[0].trigger_symbol = data.trigger_symbol;
        this.rules[0].value = data.value;
        this.silent_period = data.silent_period;
        this.active_start_time = data.active_start_time;
        this.active_end_time = data.active_end_time;
        this.resource_type = data.scope;
        this.level = data.level;
        let keyList = [];
        keyList = Object.keys(data.notification_group);
        keyList.forEach(m => {
          this.selected_group.push(m);
        });
        this.FnGetGroupList();
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    FnGetReqData() {
      if (
        this.action_type === "add" &&
        this.resource_type === 1 &&
        this.instances.length <= 0
      ) {
        Message({ message: "请选择实例", type: "error" });
        return;
      }
      let rules = [];
      this.rules.forEach((item, i) => {
        if (item.rule_name) {
          rules.push({
            name: item.rule_name,
            metrics_id: item.metrics_id.value,
            period: item.period,
            lasted_period: item.lasted_period,
            trigger_type: item.trigger_type,
            trigger_symbol: item.trigger_symbol,
            value: item.value,
            unit: item.metrics_id.unit
          });
        }
      });
      if (rules.length <= 0) {
        Message({ message: "请填写规则名称！", type: "error" });
        return;
      }
      let instancesData = [];
      if (this.action_type === "add" && this.resource_type === 1) {
        let currentInstances = this.FnSetTotalInstance();
        this.$set(this.currentApp, "instances_list", currentInstances);
        this.app_list.forEach(item => {
          if (item.instances_list) {
            instancesData.push.apply(instancesData, item.instances_list);
          }
        });
      }
      let group_list = {};
      if (this.selected_group.length > 0) {
        if (this.selected_group.length <= 10) {
          this.groupList.forEach(item => {
            if (this.selected_group.includes(item.group_id)) {
              group_list[item.group_id] = item.group_name;
            }
          });
        } else {
          Message({ message: "每条规则最多可以绑定10个联系人组！", type: "error" });
          return;
        }
      } else {
        Message({ message: "请选择要通知的对象！", type: "error" });
        return;
      }
      let reqData = {
        rules: rules,
        silent_period: this.silent_period,
        level: this.level,
        active_end_time: this.active_end_time,
        active_start_time: this.active_start_time,
        product_type: this.productDefault.type,
        notification_group: group_list,
        link_instance: instancesData,
        scope: this.resource_type
      };
      return reqData;
    },
    async createRules() {
      let reqData = await this.FnGetReqData();
      if (!reqData) {
        return;
      }
      let res = null;
      if (this.action_type === "add") {
        res = await createRules(reqData);
      } else {
        reqData.rules[0] = Object.assign(reqData.rules[0], {
          rule_id: this.rule_id
        });
        res = await updateRule(reqData);
      }
      if (res.code === "Success") {
        MessageBox.confirm(res.msg, "提示", {
          confirmButtonText: "确认",
          showCancelButton: false,
          type: "success",
          showClose: false,
          closeOnClickModal: false
        }).then(() => {
          this.$router.push({ name: "报警规则" });
        });
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    add_group() {
      this.$router.push({ name: "报警联系组" });
    },
    async FnGetGroupList() {
      let res = await getGroups();
      if (res.code === "Success") {
        this.groupList = res.data;
        this.groupList.forEach(item => {
          this.noSelected_group.push({
            key: item.group_id,
            label: item.group_name
          });
        });
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    async FnGetLinkInstance() {
      let reqData = {
        rule_id: this.rule_id,
        page_number: 1,
        page_size: 100000
      };
      let res = await linkInstance(reqData);
      if (res.code === "Success") {
        this.instance_list = res.data;
        this.instance_list.forEach(item => {
          this.instances.push(item.instance_id);
        });
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    FnSetInstance() {
      this.total_instances = 0;
      this.app_list.forEach(item => {
        if (item.id !== this.currentApp.id && item.instances_list) {
          this.total_instances += item.instances_list.length;
        }
      });
      this.total_instances += this.instances.length;
    },
    FnSetTotalInstance() {
      let instances = [];
      let index = -1;
      let id = "";
      this.instance_list.forEach(item => {
        id = this.product_type === "bms" ? item.id : item.vm_id;
        index = this.instances.indexOf(id);
        if (index > -1) {
          instances.push({
            instance_id: id,
            name: item.name,
            type: this.productDefault.type
          });
        }
      });
      return instances;
    },
    filterName(value, index) {
      let flag = false;
      this.rules.forEach((item, i) => {
        if (index !== i && item.rule_name === value) {
          flag = true;
        }
      });
      if (flag) {
        Message({ message: "规则名称重复，请重新输入！", type: "error" });
        this.$set(this.rules[index], "rule_name", "");
      }
    }
  },
  watch: {
    productDefault(newVal, oldVal) {
      if (newVal === oldVal) {
        return;
      }
      if (this.action_type === "add" && this.resource_type === "1") {
        this.FnGetAppList();
      }
    },
    currentApp(newVal, oldVal) {
      if (newVal === oldVal) {
        return;
      }
      if (oldVal) {
        let instances = this.FnSetTotalInstance();
        this.$set(oldVal, "instances_list", instances);
      }
      this.instances = [];
      this.instance_list = [];
      if (newVal.instances_list.length > 0) {
        newVal.instances_list.forEach(item => {
          this.instances.push(item.instance_id);
        });
      }
      if (this.productDefault.type === "bms") {
        this.FnGetBmsList();
      } else {
        this.FnGetVmList();
      }
    },
    resource_type(newVal, oldVal) {
      if (newVal === oldVal) {
        return;
      }
      this.total_instances = 0;
      if (newVal === 1 && this.action_type == "add") {
        this.FnGetAppList();
      }
    }
  }
};
</script>

