import http from '../http'
export function getAppList () {
    return http({
        method: 'get',
        url: '/api/vm/app/list/',
        loadingStatus: true
    })
}

export function getBmsList (data) {
    return http({
        method: 'get',
        url: '/bms/v1/list',
        params: data,
        loadingStatus: true
    })
}

export function getMonitorData (data) {
    return http({
        method: 'post',
        url: '/bms/v1/monitor',
        data: data
    })
}
export function getBmsInfo (id) {
    return http({
        method: 'get',
        url: '/bms/v1/detail/' + id
    })
}